import React, { Component } from 'react';
import './App.css';
import  'react-mdl'
import LandingPage from '../Layouts/LandingPage'
import MyContact from '../Contact/MyContact'
import MyResume from '../MyResume/MyResume'
import Project from '../Layouts/Projects'

class App extends Component {
  constructor(props) {
    super(props)
    this.state = { activeTab: 0 };
}


  render() {
    return (
    <div class="mdl-layout mdl-js-layout mdl-layout--fixed-header" >
    <header class="mdl-layout__header">
      <div class="mdl-layout__header-row">
        <span class="mdl-layout-title">Title</span>
      </div>
      <div class="mdl-layout__tab-bar mdl-js-ripple-effect">
        <a href="#scroll-tab-1" class="mdl-layout__tab is-active">Home</a>
        <a href="#scroll-tab-2" class="mdl-layout__tab">Project</a>
        <a href="#scroll-tab-3" class="mdl-layout__tab">Resume</a>
        <a href="#scroll-tab-4" class="mdl-layout__tab">AboutMe</a>
        <a href="#scroll-tab-5" class="mdl-layout__tab">Contact</a>
      </div>
    </header>
    <div class="mdl-layout__drawer">
      <span class="mdl-layout-title">Title</span>
    </div>
    <main class="mdl-layout__content">
      <section class="mdl-layout__tab-panel is-active" id="scroll-tab-1">
        <div class="page-content"> {<LandingPage/>}</div>
      </section>
      <section class="mdl-layout__tab-panel" id="scroll-tab-2">
        <div class="page-content"> {<Project/>}</div>
      </section>
      <section class="mdl-layout__tab-panel" id="scroll-tab-3">
        <div class="page-content"> {<MyResume/>}</div>
      </section>
      <section class="mdl-layout__tab-panel" id="scroll-tab-4">
        <div class="page-content"> {}</div>
      </section>
      <section class="mdl-layout__tab-panel" id="scroll-tab-5">
        <div class="page-content"> {<MyContact/>}</div>
      </section>

    </main>
  </div>
    );
  }
}

export default App;



